.load-more {
    display: flex;
    text-align: center;
    width: 100%;
    justify-content: center;
}

.load-more button {
    border: none;
    color: white;
    background-color: teal;
    padding: 10px 20px;
    margin-top: 20px;
    margin-bottom: 30px;
    border-radius: 5px;
}

.load-more button:hover {
    background-color: red;
} 
.rmdb-loadmorebtn p {
    font-family: Abel, sans-serif;
    font-size: 42px;
    padding: 20px;
}

user agent stylesheet
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    unicode-bidi: isolate;
}
.rmdb-loadmorebtn {
    background: #16d47b;
    width: 25%;
    min-height: 50px;
    text-align: center;
    color: #fff;
    cursor: pointer;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}